import React, { useMemo } from "react";
import { Tabs } from "antd";
import { useLocation, useNavigate } from "react-router-dom";
import { APP_ROUTES } from "../../contants/RoutesEnum";
import PurchaseOrderPage from "../pages/PurchaseOrder/PurchaseOrderPage";
import DraftSearchPage from "../pages/PurchaseOrder/DraftSearchPage";
import VendorSearchPage from "../pages/PurchaseOrder/VendorSearchPage";
import ShipmentSearchPage from "../pages/PurchaseOrder/ShipmentSearchPage";
import { routeFileSelector } from "../../utilz/i18n";
import { useTranslation } from "react-i18next";
import ParcelExpressPage from "../pages/Shipment/ParcelExpressPage";
import RoutingGuidePage from "../pages/RoutingGuide/RoutingGuidePage";

const PurchaseOrderLayout = () => {
  const navigate = useNavigate();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  return (
    <Tabs
      className="page-level-tab-container"
      onChange={(key) => navigate(key)}
      activeKey={activeTab}
    >
      <Tabs.TabPane
        key={APP_ROUTES.PURCHASE_ORDER_MANAGEMENT}
        tab={t("translation:default.options.purchaseOrder")}
      >
        <PurchaseOrderPage />
      </Tabs.TabPane>
      <Tabs.TabPane
        key={APP_ROUTES.PURCHASE_ORDER_VENDOR_SEARCH}
        tab={t("translation:purchase-order-view.vendorAddressSearch")}
      >
        <VendorSearchPage />
      </Tabs.TabPane>
      <Tabs.TabPane
        key={APP_ROUTES.PURCHASE_ORDER_SHIPMENT_SEARCH}
        tab={t("translation:purchase-order-view.shipmentSearch")}
      >
        <ShipmentSearchPage />
      </Tabs.TabPane>
      <Tabs.TabPane
        key={APP_ROUTES.PURCHASE_ORDER_DRAFT_SEARCH}
        tab={t("translation:purchase-order-view.draftOrderSearch")}
      >
        <DraftSearchPage />
      </Tabs.TabPane>
      <Tabs.TabPane
        key={APP_ROUTES.PURCHASE_ORDER_ROUTING_GUIDE}
        tab={"Routing Guide"}
      >
        <RoutingGuidePage />
      </Tabs.TabPane>

      {/* <Tabs.TabPane
        key={APP_ROUTES.PURCHASE_ORDER_PARCEL_EXPRESS}
        tab={t('translation:parcelExpress.titles.parcelExpress')}
      >
        <ParcelExpressPage />
      </Tabs.TabPane> */}
    </Tabs>
  );
};

export default PurchaseOrderLayout;
