import { Col, Divider, Form, Row } from "antd";
import React, { useState, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setAppLoader } from "../../../../../../redux/common/CommonActions";
import { saveShipmenOtherDetailsInProgress } from "../../../../../../redux/POShipmentSearch/POShipmentSearchActions";
import useShipmentEditPermission from "../../../../../../redux/hooks/useShipmentEditPermission";
import Button from "../../../../../components/Buttons/Button";
import SaveCancelButtons from "../../../../../components/Buttons/SaveCancelButtons";
import PrimaryLabel from "../../../../../components/Labels/PrimaryLabel";
import OtherDetailsForm from "./OtherDetailsForm";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../../../utilz/i18n'

const OtherDetails = () => {
  const [form] = Form.useForm();
  const [editMode, setEditMode] = useState(false);
  const dispatch = useDispatch();
  const hasEditPermission = useShipmentEditPermission();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  const { vendorShipmentDetailList } = useSelector((state) => state.poVendor);

  const onCancel = () => {
    setEditMode(false);
    form.setFieldsValue(vendorShipmentDetailList?.data?.othersDetails?.[0]);
  };

  const handleSubmit = (data) => {
    try {
      dispatch(setAppLoader(true));
      let payload = {
        ...data,
        shipmentKey: vendorShipmentDetailList?.data?.shipmentKey,
      };
      dispatch(saveShipmenOtherDetailsInProgress(payload));
      setTimeout(() => {
        onCancel();
      }, 0);
    } catch (error) {
      dispatch(setAppLoader(false));
    }
  };

  return (
    <div className="py-16 px-30 custom-scroll-bar h-100">
      <Form
        form={form}
        onFinish={handleSubmit}
        initialValues={{
          ...(vendorShipmentDetailList?.data
            ? vendorShipmentDetailList?.data?.othersDetails?.[0]
            : {}),
        }}
      >
        <Row className=" align-items-center" justify="space-between">
          <Col>
            <PrimaryLabel className={"fw-700 f-18"}>{t('translation:purchase-order-view.otherDetails')}</PrimaryLabel>
          </Col>
          {
            <Col>
              {!editMode ? (
                <Button
                  disabled={!hasEditPermission}
                  onClick={() => setEditMode(true)}
                >
                  {t('translation:default.buttons.edit')}
                </Button>
              ) : (
                <SaveCancelButtons
                  onSave={() => handleSubmit()}
                  onCancel={() => onCancel()}
                />
              )}
            </Col>
          }
        </Row>
        <Divider className="vw-100 ml-n40 my-16" />
        <OtherDetailsForm
          editMode={editMode}
          form={form}
          otherDetails={vendorShipmentDetailList?.data?.othersDetails?.[0]}
        />
      </Form>
    </div>
  );
};

export default OtherDetails;
