import React, { useMemo } from "react";
import WithFilter from "../../screens/common/WithFilter";

import VensorAddressSearchHeader from "../../screens/PurchaseOrderScreens/components/VensorAddressSearchHeader";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';
import VensorAddressSearchFooter from "../../screens/PurchaseOrderScreens/components/VensorAddressSearchFooter";
import VendorAddressSearch from "../../screens/PurchaseOrderScreens/VendorAddressSearch";
import VendorAddressSearchFilters from "../../screens/PurchaseOrderScreens/components/VendorAddressSearchFilters";

const VendorSearchPage = () => {
  const { pathname: activeTab} = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  
  return (
    <WithFilter
      filterTitle={`${t('translation:default.fields.address.label')} Book ${t('translation:default.buttons.search')}`}
      pageTitle={t('translation:purchase-order-view.vendorsInformation')}
      filterElement={<VendorAddressSearchFilters />}
      contentElement={<VendorAddressSearch/>
      }
      headerContent={<VensorAddressSearchHeader/>}
      footerContent={<VensorAddressSearchFooter/>}
    />
  );
};

export default VendorSearchPage;
