import { useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import Tab from "../../../../components/Tabs/Tab";
import OriginAndDestinationDetails from "./DraftOrderDetails/OriginAndDestinationDetails";
import OtherDetails from "./DraftOrderDetails/OtherDetails";
import ShipmentItemDetails from "./DraftOrderDetails/ShipmentItemDetails";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../../utilz/i18n';

const DraftOrderDetailsTab = ({ activeKey, handleTabChange }) => {
  const { vendorShipmentDetailList } = useSelector(
    (state) => state.poVendor
  );
  // const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const tabs = useMemo(
    () => [
      {
        title: t('translation:purchase-order-view.originDestinationDetails'),
        content: <OriginAndDestinationDetails shipmentDetails={vendorShipmentDetailList}/>,
        contentKey: "1",
      },
      {
        title: t('translation:purchase-order-view.shipmentItemDetails'),
        content: <ShipmentItemDetails />,
        contentKey: "2",
      },
      {
        title: t('translation:purchase-order-view.otherDetails'),
        content: <OtherDetails />,
        contentKey: "3",
      },
    ],
    [vendorShipmentDetailList?.success]
  );

  return (
    <Tab
      onChange={handleTabChange}
      contents={tabs}
      defaultActiveKey={"1"}
      activeKey={activeKey}
    />
  );
};

export default DraftOrderDetailsTab;
