import { Card, Col, Form, Row, Spin } from "antd";
import Checkbox from "../../../../components/Inputs/Checkbox";
import Paper from "../../../../components/Paper/Paper";
import PrimaryLabel from "../../../../components/Labels/PrimaryLabel";
import SecondaryLabel from "../../../../components/Labels/SecondaryLabel";
import HorizontalBar from "../../../../components/Bars/HorizontalBar";
import { ReactComponent as TruckImage } from "../../../../../assets/icons/truck-from.svg";
import { ReactComponent as FlagImage } from "../../../../../assets/icons/Flag.svg";
import {
  convertTimetoMoment,
  formateWithMoment,
  getLabelFromDate,
} from "../../../../../utilz/dates";
import { useState } from "react";
import SaveCancelButton from "../../../../components/Common/SaveCancelButton";
import { Fragment, useMemo, useEffect } from "react";
import FormSelectBox from "../../../../components/Inputs/FormSelectBox";
import FiledCard from "../../../../components/Cards/FiledCard";
import { useDispatch, useSelector } from "react-redux";
import EmptyRecords from "../../../../components/Common/EmptyRecords";
import LocationNameSelector from "../LocationNameSelector";
import FormInput from "../../../../components/Inputs/FormInput";
import CountrySelector from "../../../../components/Selectors/CountrySelector";
import StateSelector from "../../../../components/Selectors/StateSelector";
import PostalCodeInput from "../../../../components/Selectors/PostalCodeInput";
import { WEEKS_DAYS } from "../../../../../contants/common";
import FormTimePicker from "../../../../components/Inputs/FormTimePicker";
import AppointmentSelector from "../../../../components/Labels/AppointmentSelector";
import dayjs from "dayjs";
import moment from "moment";
import {
  deleteVendorAddressRequest,
  deleteVendorAddressReset,
  saveShipperInProgress,
  saveShipperInReset,
} from "../../../../../redux/POV/POVActions";
import { setAppLoader } from "../../../../../redux/common/CommonActions";
let req = {};
let deleteRecord = {};
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../utilz/i18n";

const VendorSearchCard = ({
  saveShipper,
  deleteVendorAddress,
  data,
  index,
  updateEditRecord = () => {},
  updateSelectedItems = () => {},
}) => {
  const [form] = Form.useForm();
  const locationCountryData = Form.useWatch("country", form);
  const dispatch = useDispatch();
  const { selectedCompany, user } = useSelector((state) => state.auth);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const [editMode, setEditMode] = useState(false);

  const vendorSearchListRender = (data) => {
    return (
      <Row gutter={16} className="mt-10">
        <Col sm={8}>
          <Row>
            <Col sm={24}>
              <FiledCard
                textClass="text-with-line w-100"
                title={t("translation:purchase-order-view.locationDetails")}
              ></FiledCard>
            </Col>
          </Row>
          <Row className="mt-15">
            <Col sm={24}>
              {data.isEditMode === false && (
                <FiledCard
                  title={t(
                    "translation:purchase-order-view.fields.locationAddress.label"
                  )}
                >
                  <PrimaryLabel normal={true} className={"f-14 fw-600"}>
                    {data.address}
                  </PrimaryLabel>
                  <div>
                    <SecondaryLabel>
                      {data.city}, {data.stateDesc}, {data.countryName} ,
                      {data.zip}
                    </SecondaryLabel>
                  </div>
                </FiledCard>
              )}
              {data.isEditMode === true && (
                <Fragment>
                  <Row>
                    <Col sm={24}>
                      <FormInput
                        margin={"mb-15"}
                        editMode={true}
                        label={t(
                          "translation:purchase-order-view.fields.locationAddress.label"
                        )}
                        name={"address"}
                        placeholder={t(
                          "translation:purchase-order-view.fields.locationAddress.label"
                        )}
                        rules={[
                          {
                            required: true,
                            message: t(
                              "translation:purchase-order-view.pleaseEnterLocationAddress"
                            ),
                          },
                        ]}
                        maxLength={"100"}
                      />
                    </Col>
                  </Row>
                  <Row gutter={12}>
                    <Col sm={12}>
                      <CountrySelector
                        name={`country`}
                        label={t(
                          "translation:purchase-order-view.fields.locationCountry.label"
                        )}
                        editMode={true}
                        margin={"mb-15"}
                        rules={[
                          {
                            required: true,
                            message: t(
                              "translation:purchase-order-view.pleaseSelectCountry"
                            ),
                          },
                        ]}
                      />
                    </Col>
                    <Col sm={12}>
                      <FormInput
                        name={`city`}
                        label={t(
                          "translation:purchase-order-view.fields.locationCity.label"
                        )}
                        editMode={true}
                        margin={"mb-15"}
                        rules={[
                          {
                            required: true,
                            message: t("translation:default.fields.city.label"),
                          },
                        ]}
                      />
                    </Col>
                  </Row>
                  <Row gutter={12}>
                    <Col sm={12}>
                      <StateSelector
                        name={`state`}
                        label={t("translation:default.fields.state.label")}
                        editMode={true}
                        margin={"mb-15"}
                        countryCode={locationCountryData}
                        rules={[
                          {
                            required: true,
                            message: t(
                              "translation:purchase-order-view.pleaseChooseState"
                            ),
                          },
                        ]}
                      />
                    </Col>
                    <Col sm={12}>
                      <FormInput
                        margin={"mb-15"}
                        editMode={true}
                        label={t("translation:default.fields.postalCode.label")}
                        name={"zip"}
                        placeholder={t(
                          "translation:default.fields.postalCode.label"
                        )}
                        rules={[
                          {
                            required: true,
                            message: t(
                              "translation:default.fields.postalCode.message"
                            ),
                          },
                        ]}
                        maxLength={10}
                      />
                    </Col>
                  </Row>
                </Fragment>
              )}
            </Col>
          </Row>
        </Col>
        <Col sm={8}>
          <Row>
            <Col sm={24}>
              <FiledCard
                textClass="text-with-line w-100"
                title={t(
                  "translation:purchase-order-view.wareHouseContactDetails"
                )}
              ></FiledCard>
            </Col>
          </Row>
          <Row className="mt-15">
            <Col sm={24}>
              {data.isEditMode === false && (
                <FiledCard
                  title={t("translation:purchase-order-view.contactPerson")}
                >
                  <PrimaryLabel normal={true} className={"f-14 fw-600"}>
                    {data.contactName}
                  </PrimaryLabel>
                </FiledCard>
              )}
              {data.isEditMode === true && (
                <Fragment>
                  <Row gutter={12}>
                    <Col sm={24}>
                      <FormInput
                        margin={"mb-15"}
                        editMode={true}
                        label={t(
                          "translation:purchase-order-view.contactPerson"
                        )}
                        name={"contactName"}
                        placeholder={t(
                          "translation:purchase-order-view.contactPerson"
                        )}
                        maxLength={30}
                      />
                    </Col>
                  </Row>
                  <Row className="mt-5" gutter={12}>
                    <Col sm={12}>
                      <FiledCard
                        title={t("translation:purchase-order-view.telephoneNo")}
                      ></FiledCard>
                    </Col>
                    <Col sm={6}>
                      <FiledCard
                        title={t("translation:purchase-order-view.extension")}
                      ></FiledCard>
                    </Col>
                  </Row>
                  <Row gutter={12} className="mt-5">
                    {/* <Col sm={4} className="d-flex gap-1">
                      <FormInput
                        editMode={true}
                        name={"phone"}
                        placeholder=""
                      />
                    </Col> */}
                    <Col sm={12}>
                      <FormInput
                        editMode={true}
                        name={"businessPhone"}
                        placeholder={t(
                          "translation:purchase-order-view.telephoneNo"
                        )}
                        maxLength={15}
                      />
                    </Col>
                    <Col sm={6}>
                      <FormInput
                        editMode={true}
                        // label={"Extension"}
                        name={"phoneExt"}
                        placeholder={t(
                          "translation:purchase-order-view.extension"
                        )}
                        maxLength={10}
                      />
                    </Col>
                  </Row>

                  <Row gutter={12} className="mt-5">
                    <Col sm={24}>
                      <FormInput
                        editMode={true}
                        label={t("translation:default.fields.emailAddress")}
                        name={"email"}
                        placeholder={t(
                          "translation:default.fields.emailAddress"
                        )}
                      />
                    </Col>
                  </Row>
                </Fragment>
              )}
            </Col>
          </Row>
          {data.isEditMode === false && (
            <Row className="mt-15">
              <Col sm={8}>
                <FiledCard
                  title={t("translation:purchase-order-view.telephoneNo")}
                >
                  <SecondaryLabel normal={true} className="font-weight-400">
                    {data.businessPhone}
                  </SecondaryLabel>
                </FiledCard>
              </Col>
              <Col sm={4}>
                <FiledCard
                  title={t("translation:purchase-order-view.extension")}
                >
                  <SecondaryLabel normal={true} className="font-weight-400">
                    {data.phoneExt}
                  </SecondaryLabel>
                </FiledCard>
              </Col>
              <Col sm={12}>
                <FiledCard title={t("translation:default.fields.emailAddress")}>
                  <SecondaryLabel normal={true} className="font-weight-400">
                    {data.email}
                  </SecondaryLabel>
                </FiledCard>
              </Col>
            </Row>
          )}
        </Col>
        <Col sm={8}>
          <Row>
            <Col sm={24}>
              <FiledCard
                textClass="text-with-line w-100"
                title={t("translation:purchase-order-view.warehouseDetails")}
              ></FiledCard>
            </Col>
          </Row>
          {data?.isEditMode === false && (
            <Fragment>
              <Row className="mt-15">
                <Col sm={12}>
                  <FiledCard title={t("translation:default.fields.daysOfWeek")}>
                    <SecondaryLabel normal={true} className="font-weight-400">
                      {data.daysOfWeek}
                    </SecondaryLabel>
                  </FiledCard>
                </Col>
                <Col sm={12}>
                  <FiledCard
                    title={t("translation:default.fields.hoursOfOperations")}
                  >
                    <SecondaryLabel normal={true} className="font-weight-400">
                      {data.hoursOfOperation}
                    </SecondaryLabel>
                  </FiledCard>
                </Col>
              </Row>
              <Row className="mt-15">
                <Col sm={24}>
                  <FiledCard
                    title={t(
                      "translation:default.fields.appointmentType.label"
                    )}
                  >
                    <SecondaryLabel normal={true} className="font-weight-400">
                      {data.appointmentType}
                    </SecondaryLabel>
                  </FiledCard>
                </Col>
              </Row>
            </Fragment>
          )}
          {data?.isEditMode === true && (
            <Fragment>
              <Row className="mt-15">
                <FiledCard
                  title={t("translation:default.fields.daysOfWeek")}
                ></FiledCard>
              </Row>
              <Row className="mt-5">
                <Col sm={12}>
                  <FormSelectBox
                    options={WEEKS_DAYS}
                    editMode={true}
                    singleLineMode={true}
                    label={t("translation:default.fields.from.label")}
                    colon={true}
                    name={"fromWeekDay"}
                    placeholder={t(
                      "translation:default.fields.from.placeholder"
                    )}
                  />
                </Col>
                <Col sm={12}>
                  <FormSelectBox
                    options={WEEKS_DAYS}
                    editMode={true}
                    singleLineMode={true}
                    label={t("translation:default.fields.to.label")}
                    colon={true}
                    name={"toWeekDay"}
                    placeholder={t("translation:default.fields.to.placeholder")}
                  />
                </Col>
              </Row>
              <Row className="mt-5">
                <FiledCard
                  title={t("translation:default.fields.hoursOfOperations")}
                ></FiledCard>
              </Row>
              <Row gutter={12}>
                <Col sm={12}>
                  <FormTimePicker
                    name={"fromOperationDay"}
                    editMode={true}
                    placeholder={t("translation:default.fields.from.label")}
                    label={t("translation:default.fields.from.label")}
                    singleLineMode={true}
                    inputLabelClasss="f-14 text-highlight"
                  />
                </Col>

                <Col sm={12}>
                  <FormTimePicker
                    name={"toOperationDay"}
                    editMode={true}
                    label={t("translation:default.fields.to.label")}
                    singleLineMode={true}
                    inputLabelClasss="f-14 text-highlight"
                  />
                </Col>
              </Row>
              <Row className="mt-5">
                <AppointmentSelector
                  label={t("translation:default.fields.appointmentType.label")}
                  name={"appointmentType"}
                  placeholder={t(
                    "translation:default.fields.appointmentType.label"
                  )}
                  editMode={true}
                  rules={[
                    {
                      required: true,
                      message: t(
                        "translation:default.fields.appointmentType.choose"
                      ),
                    },
                  ]}
                />
              </Row>
            </Fragment>
          )}
        </Col>
      </Row>
    );
  };

  const removeUndefinedValues = (obj) => {
    const newObj = {};
    for (const key in obj) {
      if (obj[key] !== undefined) {
        newObj[key] = obj[key];
      }
    }
    return newObj;
  };

  const saveShipperInfo = (formData, data) => {
    req = {
      ...data,
      ...formData,
      vendorKey: selectedCompany?.companyId,
      hoursOfOperation:
        (formData.fromOperationDay
          ? moment(formData?.fromOperationDay).format("hh:mm a")
          : "") +
        " - " +
        (formData.toOperationDay
          ? moment(formData?.toOperationDay).format("hh:mm a")
          : ""),
      daysOfWeek:
        (formData?.fromWeekDay || "") + "-" + (formData?.toWeekDay || ""),
    };
    let values = removeUndefinedValues(req);
    dispatch(saveShipperInProgress(values));
  };

  useEffect(() => {
    if (saveShipper?.progressing === true) {
      dispatch(setAppLoader(true));
    } else if (
      saveShipper?.progressing === false &&
      saveShipper?.success === true
    ) {
      dispatch(setAppLoader(false));
      updateEditRecord(req, "refresh");
      dispatch(saveShipperInReset());
    }
  }, [saveShipper]);

  useEffect(() => {
    if (deleteVendorAddress?.progressing === true) {
      dispatch(setAppLoader(true));
    } else if (
      deleteVendorAddress?.progressing === false &&
      deleteVendorAddress?.success === true
    ) {
      dispatch(setAppLoader(false));
      updateEditRecord(deleteRecord, "delete");
      dispatch(deleteVendorAddressReset());
    } else {
      dispatch(setAppLoader(false));
    }
  }, [deleteVendorAddress]);

  const getInitialVal = () => {
    return {
      name: data?.name,
      address: data?.address,
      country: data?.country,
      city: data?.city,
      state: data?.state,
      zip: data?.zip,
      contactName: data?.contactName,
      businessPhone: data.businessPhone?.replace(/[^0-9]/g, ""),
      phoneExt: data?.phoneExt,
      email: data?.email,
      fromWeekDay: data?.daysOfWeek?.split("-")[0],
      toWeekDay: data?.daysOfWeek?.split("-")[1],
      fromOperationDay: moment(
        moment(new Date()).format("MM/DD/YYYY") +
          " " +
          moment(data.hoursOfOperation?.split("-")[0], ["h:mm A"]).format(
            "HH:mm"
          )
      ),
      toOperationDay: moment(
        moment(new Date()).format("MM/DD/YYYY") +
          " " +
          moment(data.hoursOfOperation?.split("-")[1], ["h:mm A"]).format(
            "HH:mm"
          )
      ),
      appointmentType: data?.appointmentType,
    };
  };

  return (
    <Form
      onFinish={(formValues) => saveShipperInfo(formValues, data)}
      form={form}
      name={`form_${data.vendorAddressKey}`}
      {...{
        initialValues: data.vendorAddressKey > 0 ? getInitialVal() : {},
      }}
    >
      <Card
        key={`key-${data.vendorAddressKey}`}
        title={
          <Row>
            <Col sm={8} className="d-flex gap-half align-items-center">
              <Checkbox
                onClick={() => updateSelectedItems(data)}
                checked={data.isChecked}
                name={`checked_${data.vendorAddressKey}`}
              />
              {data.isEditMode === true && (
                <FormInput
                  margin={"m-0"}
                  editMode={true}
                  label={t(
                    "translation:purchase-order-view.fields.locationName.label"
                  )}
                  name={"name"}
                  singleLineMode={true}
                  placeholder={t(
                    "translation:purchase-order-view.fields.locationName.label"
                  )}
                  rules={[
                    {
                      required: true,
                      message: t(
                        "translation:purchase-order-view.pleaseEnterLocationName"
                      ),
                    },
                  ]}
                  maxLength={50}
                />
              )}
              {data.isEditMode === false && (
                <FiledCard
                  title={`${t(
                    "translation:purchase-order-view.fields.locationName.label"
                  )}:`}
                  textClass="d-flex align-items-center gap-half mt-2"
                >
                  <PrimaryLabel className="font-weight-600">
                    {data.name}
                  </PrimaryLabel>
                </FiledCard>
              )}
            </Col>
          </Row>
        }
        extra={
          // <SaveCancelButtons
          //     onSave={() => form.submit()}
          //     onCancel={() => {}}
          //   />

          <SaveCancelButton
            isNew={false}
            editMode={data.isEditMode}
            setEditMode={() => updateEditRecord(data, true)}
            onCancel={() => updateEditRecord(data, false)}
            handleRemove={() => {
              deleteRecord = data;
              dispatch(
                deleteVendorAddressRequest({
                  vendorAddressKey: data?.vendorAddressKey.toString(),
                })
              );
            }}
            onSave={() => {}}
          />
        }
        className="paper-container border-muted mb-0 po-shipment-search"
        style={{ border: "none" }}
      >
        <Fragment>{vendorSearchListRender(data)}</Fragment>
      </Card>
    </Form>
  );
};

export default VendorSearchCard;
