import React, { useMemo } from "react";
import { Col, Row, Form, Input } from "antd";
import FormInput from "../../../../components/Inputs/FormInput";
import CountrySelector from "../../../../components/Selectors/CountrySelector";
import PostalCodeInput from "../../../../components/Selectors/PostalCodeInput";
import StateSelector from "../../../../components/Selectors/StateSelector";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../utilz/i18n";

const VendorSearch = ({ form, onChangeContent }) => {
  const locationCountryData = Form.useWatch("locationCountry", form);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  const handleZipCodeResult = (data) => {
    form.setFieldsValue({
      ["locationState"]: data?.state,
      [`locationCity`]: data?.city,
    });
  };

  return (
    <Row>
      <Col sm={24}>
        <FormInput
          margin={"mb-15"}
          editMode={true}
          label={t("translation:purchase-order-view.fields.locationName.label")}
          name={"locationName"}
          placeholder={t(
            "translation:purchase-order-view.fields.locationName.label"
          )}
        />
      </Col>
      <Col sm={24}>
        <FormInput
          margin={"mb-15"}
          editMode={true}
          label={t(
            "translation:purchase-order-view.fields.locationAddress.label"
          )}
          name={"locationAddress"}
          placeholder={t(
            "translation:purchase-order-view.fields.locationAddress.label"
          )}
        />
      </Col>
      <Col sm={24}>
        <CountrySelector
          name={`locationCountry`}
          label={t(
            "translation:purchase-order-view.fields.locationCountry.label"
          )}
          editMode={true}
          margin={"mb-15"}
        />
      </Col>
      <Col sm={24}>
        <PostalCodeInput
          margin="mb-10"
          name={`locationPostalCode`}
          label={t(
            "translation:purchase-order-view.fields.locationPostalCode.label"
          )}
          editMode={true}
          handleZipDetails={(data) => handleZipCodeResult(data)}
          countryCode={locationCountryData}
        />
      </Col>
      <Col sm={24}>
        <StateSelector
          name={`locationState`}
          label={t(
            "translation:purchase-order-view.fields.locationStateProvince.label"
          )}
          editMode={true}
          margin={"mb-15"}
          countryCode={locationCountryData}
        />
      </Col>
    </Row>
  );
};

export default VendorSearch;
