import React, { useMemo } from "react";
import WithFilter from "../../screens/common/WithFilter";
import ShipmentSearchHeader from "../../screens/PurchaseOrderScreens/ShipmentSearch/ShipmentSearchHeader";
import ShipmentSearchFilter from "../../screens/PurchaseOrderScreens/ShipmentSearch/ShipmentSearchFilter";
import ShipmentSearchContent from "../../screens/PurchaseOrderScreens/ShipmentSearch/ShipmentSearchContent";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../utilz/i18n";
import { useTranslation } from "react-i18next";
import ShipmentSearchFooter from "../../screens/PurchaseOrderScreens/ShipmentSearch/ShipmentSearchFooter";

const ShipmentSearchPage = () => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <WithFilter
      filterTitle={t('translation:shipment.filter.titles.shipmentSearch')}
      pageTitle={t('translation:purchase-order-view.shipmentInformation')}
      headerContent={<ShipmentSearchHeader />}
      filterElement={<ShipmentSearchFilter />}
      contentElement={<ShipmentSearchContent />}
      footerContent={<ShipmentSearchFooter/>}
    />
  );
};

export default ShipmentSearchPage;
