import { Col, Form, Row } from "antd";
import React, { useMemo } from "react";
import { shallowEqual, useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";
import Button from "../../../components/Buttons/Button";
import VendorSearch from "./Filters/VendorSearch";
import moment from "moment";
import { getVendorSearchClear, getVendorSearchRequest, updateVendorSearchFilter } from "../../../../redux/POV/POVActions";

function VendorAddressSearchFilters() {
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const vendorSearchListFilter = useSelector(
    (state) => state.poVendor.vendorSearchListFilter,
    shallowEqual
  );
  const selectedCompany = useSelector(
    (state) => state?.auth.selectedCompany,
    shallowEqual
  );

  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  const onChangeContent = (data) => {
    form.setFieldsValue(data);
  };

  const handleFilterSubmit = (data) => {
    dispatch(getVendorSearchClear());
    let reqParams = {
      ...vendorSearchListFilter,
      country: data.locationCountry,
      name: data.locationName,
      address: data.locationAddress,
      city: data.locationCity,
      state: data.locationState,
      zip: data.locationPostalCode,
      vendorKey: selectedCompany?.companyId,
      pageIndex: 1,
    };
    Object.keys(reqParams).forEach((key) =>
      reqParams[key] === undefined ? (reqParams[key] = "") : {}
    );
    dispatch(updateVendorSearchFilter(reqParams));
    dispatch(getVendorSearchRequest(reqParams));
  };

  const handleClear = () => {
    form.resetFields();
  };

  return (
    <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
      <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1">
        <Form
          onFinish={handleFilterSubmit}
          form={form}
          initialValues={{}}
        >
          <div className="filter-collapse-container px-16">
            <VendorSearch form={form} onChangeContent={onChangeContent} />
          </div>
        </Form>
      </div>
      <Row className="px-15" gutter={15}>
        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"default"}
            onClick={handleClear}
            buttonType="button"
          >
            {t("translation:default.buttons.clear")}
          </Button>
        </Col>
        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"primary"}
            onClick={() => form.submit()}
            buttonType="submit"
          >
            {t("translation:default.buttons.search")}
          </Button>
        </Col>
      </Row>
    </div>
  );
}
export default VendorAddressSearchFilters;
