import React, { useMemo, useEffect, useState } from "react";
import { Col, Form, Row } from "antd";
import { useDispatch, useSelector } from "react-redux";
import Button from "../../../components/Buttons/Button";
import ShipmentSearchFilterContent from "./Component/Search/ShipmentSearchFilterContent";
import {
  getShipmentInfoListInProgress,
  getShipmentInfoListListInReset,
  updateShipmentInfoListRequest,
} from "../../../../redux/POShipmentSearch/POShipmentSearchActions";
import { SHIPMENT_SEARCH_DATE_FILEDS } from "../../../../contants/Fields";
import { PURCHASE_ORDER_DATE_FORMAT } from "../../../../contants/common";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const ShipmentSearchFilter = () => {
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const { shipmentInfoList, shipmentInfoListReq } = useSelector(
    (state) => state.poShipmentSearch
  );

  const {
    user: { data: userDetails },
    selectedCompany,
  } = useSelector((state) => state?.auth);

  const getShipmentSearchList = (data) => {
    dispatch(getShipmentInfoListListInReset());
    for (const key in data) {
      if (SHIPMENT_SEARCH_DATE_FILEDS.includes(key) && data[key]) {
        data[key] = data[key].format(PURCHASE_ORDER_DATE_FORMAT);
      }
    }
    Object.keys(data).forEach((key) => {
      if (data[key] === undefined) {
        data[key] = "";
      }
    });

    let req = {
      ...shipmentInfoListReq,
      ...data,
      pageIndex:1
    };
    dispatch(
      updateShipmentInfoListRequest({
      ...data,
      pageIndex:1
      })
    );
    dispatch(getShipmentInfoListInProgress(req));
  };

  useEffect(() => {
    setTimeout(() => {
      form.resetFields();
    }, 1000);
    dispatch(getShipmentInfoListListInReset())
    dispatch(
      updateShipmentInfoListRequest({
        pageIndex: 1,
        vendorKey: selectedCompany?.companyId,
      })
    );
  }, [selectedCompany?.companyId]);

  const customerKey = Form.useWatch("customerKey", form);


  useEffect(() => {
    if (selectedCompany?.companyId && customerKey) {
      dispatch(getShipmentInfoListListInReset())
      dispatch(
        updateShipmentInfoListRequest({
          customerKey:customerKey,
          pageIndex: 1,
          vendorKey: selectedCompany?.companyId,
        })
      );
      dispatch(getShipmentInfoListInProgress({
        ...shipmentInfoListReq,
        customerKey:customerKey
      }));
    }
    
  }, [customerKey]);

  return (
    <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
      <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1">
        <Form
          onFinish={getShipmentSearchList}
          form={form}
          initialValues={{
            isActive: true,
            isApproved: true,
            shipmentType:"Final",
            customerKey:customerKey
          }}
        >
          <ShipmentSearchFilterContent form={form} />
        </Form>
      </div>
      <Row className="px-15" gutter={15}>
        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"default"}
            onClick={() => {
              let customerKey = form.getFieldValue('customerKey');
              form.resetFields();
              form.setFieldsValue({
                customerKey:customerKey,
                isActive: true,
                isApproved: true,
                shipmentType:"Final"
              });
            
            }}
            buttonType="button"
          >
            {t('translation:default.buttons.clear')}
          </Button>
        </Col>
        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"primary"}
            onClick={() => form.submit()}
            buttonType="submit"
          >
            {t('translation:default.buttons.search')}
          </Button>
        </Col>
      </Row>
    </div>
  );
};

export default ShipmentSearchFilter;
