import React, { Fragment, useMemo, useState } from "react";
import TextButton from "../../../components/Buttons/TextButton";
import { ReactComponent as ExportIcon } from "../../../../assets/icons/export-icon.svg";
import CommonModal from "../../../components/Modals/CommonModal";
import VendorAddressExport from "./VendorAddressExport";
import { Form } from "antd";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

function VendorAddressExportDetails({ filterCheckedItem }) {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  const [openModel, setOpenModel] = useState(false);
  const [exportForm] = Form.useForm();

  return (
    <Fragment>
      <TextButton
        icon={<ExportIcon />}
        onClick={() => setOpenModel(!openModel)}
        disabled={filterCheckedItem.length === 0}
      >
        <label className="ml-5">
          {t("translation:default.buttons.export")}
        </label>
      </TextButton>
      <CommonModal
        className="vw-80"
        open={openModel}
        title={t("translation:purchase-order-view.vendorAddressSearchExport")}
        okText={t("translation:default.buttons.export")}
        cancelText={t("translation:default.buttons.close")}
        onClose={() => {
          setOpenModel(false);
        }}
        onCancel={() => {
          setOpenModel(false);
        }}
        onOk={() => {
          exportForm.submit();
        }}
      >
        <VendorAddressExport form={exportForm} />
      </CommonModal>
    </Fragment>
  );
}

export default VendorAddressExportDetails;
