import React, { useMemo } from "react";
import { Col, Row, Spin } from "antd";
import { ReactComponent as ExportIcon } from "../../../../assets/icons/export-icon.svg";
import { useDispatch, useSelector } from "react-redux";
import ShipmentSearchListFilter from "./Component/Header/ShipmentSearchListFilter";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';
import TextButton from "../../../components/Buttons/TextButton";
import { setShipmentSearchExportModal, updateShipmentInfoCheckedItems } from "../../../../redux/POShipmentSearch/POShipmentSearchActions";
import FormLabel from "../../../components/Labels/FormLabel";
import FormCheckBox from "../../../components/Inputs/FormCheckBox";
import BatchLabel from "../../../components/Labels/BatchLabel";
import { useLocation } from "react-router-dom";

const ShipmentSearchHeader = () => {
  const dispatch = useDispatch();
  const { shipmentInfoList } = useSelector((state) => state.poShipmentSearch);
  let { savedShipmentDatas } = useSelector((state) => state.poVendor);

  let findCheckedItems = shipmentInfoList?.data?.filter(
    (item) => item.isChecked === true
  );
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const exportData = () => {
    dispatch(
      setShipmentSearchExportModal({ show: true, data: findCheckedItems || [] })
    );
  };

  const handleSelectAllCheckBox = (e) => {
    dispatch(updateShipmentInfoCheckedItems(e.target.checked));
  };

  return (
    <Row justify="space-between" className={`py-15 px-14 align="middle`}>
      <Col className="flex-1">
        <Row align={"middle"} gutter={20}>
          <Col sm={12}>
            <div className="d-flex gap-1">
              <FormCheckBox
                checkBoxClass="normal-checkbox"
                checked={
                  findCheckedItems.length > 0 &&
                  findCheckedItems.length === shipmentInfoList?.data.length
                    ? true
                    : false
                }
                label={
                  <FormLabel className="white-space-nowrap">
                    {t('translation:default.fields.selectAll')}
                  </FormLabel>
                }
                className={"m-0"}
                onChange={handleSelectAllCheckBox}
              />
              <ShipmentSearchListFilter />
            </div>
          </Col>
          <Col sm={8}>
            {savedShipmentDatas && savedShipmentDatas[0]?.shipmentNumber && savedShipmentDatas[0]?.shipmentType === "Final" ? (
              <label className="ml-5">
                <b>{t('translation:purchase-order-view.lastCreatedShipmentNumber')} : </b>
                <BatchLabel type="open">
                  {savedShipmentDatas[0]?.shipmentNumber}
                </BatchLabel>
              </label>
            ) : (
              ""
            )}
          </Col>
          <Col sm={4}>
            <div className="d-flex justify-content-end gap-2 align-items-center">
              <TextButton
                icon={<ExportIcon />}
                onClick={exportData}
                disabled={!(findCheckedItems.length > 0)}
              >
                <label className="ml-5">{t('translation:default.buttons.export')}</label>
              </TextButton>
            </div>
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default ShipmentSearchHeader;
