import React, {useMemo} from "react";
import { Col, Row } from "antd";
import FooterContentCard from "../../common/FooterContentCard";
import { ReactComponent as TotalRecordsIcon } from "../../../../assets/icons/check-list.svg";
import { useSelector } from "react-redux";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const ShipmentDraftSearchFooter = () => {
    const { shipmentInfoList } =
    useSelector((state) => state.poShipmentDraftSearch);

  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <Row className=" footer-container background-header px-20  py-12 d-flex justify-content-end">
      <Col>
        <FooterContentCard
          count={shipmentInfoList?.totalCount || 0}
          label= {t('translation:purchase-order-view.totalOrders')}
          Icon={TotalRecordsIcon}
        />
      </Col>
    </Row>
  );
};

export default ShipmentDraftSearchFooter;
