import { Col, Popconfirm, Row, Spin } from "antd";
import { ReactComponent as ExportIcon } from "../../../../assets/icons/export-icon.svg";
import { useDispatch, useSelector } from "react-redux";
import TextButton from "../../../components/Buttons/TextButton";
import ShipmentDraftSearchListFilter from "./Component/Header/ShipmentDraftSearchListFilter";
import {
  setShipmentDraftSearchExportModal,
  updateShipmentDraftInfoCheckedItems,
} from "../../../../redux/POShipmentDraftSearch/POShipmentDraftSearchActions";
import FormLabel from "../../../components/Labels/FormLabel";
import FormCheckBox from "../../../components/Inputs/FormCheckBox";
import BatchLabel from "../../../components/Labels/BatchLabel";
import { ReactComponent as DeleteIcon } from "../../../../assets/icons/delete-icon.svg";
import { setAppLoader } from "../../../../redux/common/CommonActions";
import { cancelShipementInProgress } from "../../../../redux/POV/POVActions";
import { useMemo } from "react";
import { routeFileSelector } from "../../../../utilz/i18n";

import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';

const ShipmentDraftSearchHeader = () => {
  const dispatch = useDispatch();
  const { shipmentInfoList } = useSelector(
    (state) => state.poShipmentDraftSearch
  );
  let { savedShipmentDatas } = useSelector((state) => state.poVendor);

  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  let findCheckedItems = shipmentInfoList?.data?.filter(
    (item) => item.isChecked === true
  );

  const exportData = () => {
    dispatch(
      setShipmentDraftSearchExportModal({
        show: true,
        data: findCheckedItems || [],
      })
    );
  };

  const handleSelectAllCheckBox = (e) => {
    dispatch(updateShipmentDraftInfoCheckedItems(e.target.checked));
  };

  const handleDelete = () => {
    let getShipmentId = findCheckedItems.map(item=>item.shipmentKey);
    dispatch(setAppLoader(true));
    dispatch(cancelShipementInProgress({ shipmentKey:getShipmentId.join(",") }));
  }

  return (
    <Row justify="space-between" className={`py-15 px-14 align="middle`}>
      <Col className="flex-1">
        <Row align={"middle"} gutter={20}>
          <Col sm={12}>
            <div className="d-flex gap-1">
              <FormCheckBox
                checkBoxClass="normal-checkbox"
                checked={
                  findCheckedItems.length > 0 &&
                  findCheckedItems.length === shipmentInfoList?.data.length
                    ? true
                    : false
                }
                label={
                  <FormLabel className="white-space-nowrap">
                    {t('translation:default.fields.selectAll')}
                  </FormLabel>
                }
                className={"m-0"}
                onChange={handleSelectAllCheckBox}
              />
              <ShipmentDraftSearchListFilter />
            </div>
          </Col>
          <Col sm={8}>
            {savedShipmentDatas &&
            savedShipmentDatas[0]?.shipmentNumber &&
            savedShipmentDatas[0]?.shipmentType === "Draft" ? (
              <label className="ml-5">
                <b>{t('translation:purchase-order-view.lastCreatedShipmentNumber')} : </b>
                <BatchLabel type="open">
                  {savedShipmentDatas[0]?.shipmentNumber}
                </BatchLabel>
              </label>
            ) : (
              ""
            )}
          </Col>
          <Col sm={4}>
            <div className="d-flex justify-content-end gap-1 align-items-center">
              <TextButton
                icon={<DeleteIcon />}
                disabled={!(findCheckedItems.length > 0)}
              >
                <Popconfirm
                  placement="topRight"
                  title={t('translation:default.fields.deleteConfirm.title')}
                  onConfirm={() => handleDelete()}
                  okText={t('translation:default.options.yes')}
                  cancelText={t('translation:default.buttons.cancel')}
                  overlayClassName=""
                >
                  <label className="ml-5">{t('translation:default.buttons.delete')}</label>
                </Popconfirm>
              </TextButton>
              <TextButton
                icon={<ExportIcon />}
                onClick={exportData}
                disabled={!(findCheckedItems.length > 0)}
              >
                <label className="ml-5">{t('translation:default.buttons.export')}</label>
              </TextButton>
            </div>
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

export default ShipmentDraftSearchHeader;
