import React, { useEffect, useState, useMemo, Fragment } from "react";
import { Col, Form, Row } from "antd";
import TextButton from "../../../components/Buttons/TextButton";
// import Checkbox from "../../../components/Inputs/Checkbox";
import Button from "../../../components/Buttons/Button";
import {
  addVendorRecord,
  getVendorSearchClear,
  getVendorSearchRequest,
  getVendorSearchUpdate,
  setVendorExportModal,
  updateVendorSearchFilter,
} from "../../../../redux/POV/POVActions";
import { ReactComponent as DeleteIcon } from "../../../../assets/icons/delete-icon.svg";
import FormSelectBox from "../../../components/Inputs/FormSelectBox";
import IconButton from "../../../components/Buttons/IconButton";
import SvgConverter from "../../../components/Common/SvgConverter";
import { SORTING_ORDERS } from "../../../../contants/common";
import { ReactComponent as FilterAscIcon } from "../../../../assets/icons/filter-asc.svg";
import { ReactComponent as FilterDescIcon } from "../../../../assets/icons/filter-desc.svg";
import { useDispatch, useSelector, shallowEqual } from "react-redux";
import Checkbox from "antd/lib/checkbox/Checkbox";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../utilz/i18n";
import i18n from "i18next";
import VendorAddressExportDetails from "./VendorAddressExportDetails";

let sortingValuesList = [
  {
    label: i18n.t("translation:purchase-order-view.vendorsInformation"),
    value: "name",
  },
  {
    label: i18n.t("translation:default.fields.contactName.label"),
    value: "contactName",
  },
  {
    label: i18n.t("translation:default.fields.email"),
    value: "email",
  },
  {
    label: i18n.t("translation:default.fields.city.label"),
    value: "city",
  },
  {
    label: i18n.t("translation:default.fields.state.label"),
    value: "state",
  },
  {
    label: i18n.t("translation:default.fields.zip"),
    value: "zip",
  },
  {
    label: i18n.t("translation:default.fields.country.label"),
    value: "country",
  },
  {
    label: i18n.t("translation:default.fields.address.label"),
    value: "address",
  },
  {
    label: i18n.t("translation:default.fields.daysOfWeek"),
    value: "daysOfWeek",
  },
  {
    label: i18n.t("translation:default.fields.hoursOfOperations"),
    value: "hoursOfOperation",
  },
  {
    label: i18n.t("translation:default.fields.appointmentType.label"),
    value: "appointmentType",
  },
];

const VensorAddressSearchHeader = () => {
  const [form] = Form.useForm();
  const orderByColumn = Form.useWatch("orderByColumn", form);
  const [selectedOrderType, setSelectedOrderType] = useState(
    SORTING_ORDERS.ASC
  );
  let { vendorSearchList, vendorSearchListFilter } = useSelector(
    (state) => state.poVendor
  );
  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const selectedCompany = useSelector(
    (state) => state.auth.selectedCompany,
    shallowEqual
  );

  const handleSortingToggle = (oldType) => {
    let sortingType;
    if (oldType === SORTING_ORDERS.ASC) {
      sortingType = SORTING_ORDERS.DESC;
    } else if (oldType === SORTING_ORDERS.DESC) {
      sortingType = SORTING_ORDERS.ASC;
    }
    return sortingType;
  };

  useEffect(() => {
    form.setFieldValue("orderByColumn", null);
  }, []);

  useEffect(() => {
    if (selectedCompany && selectedCompany.companyId) {
      let reqParams = {
        ...vendorSearchListFilter,
        pageIndex: 1,
        orderByColumn: orderByColumn,
        orderBySort: selectedOrderType,
      };
      dispatch(getVendorSearchClear());
      dispatch(updateVendorSearchFilter(reqParams));
      dispatch(getVendorSearchRequest(reqParams));
    }
  }, [orderByColumn, selectedOrderType]);

  let filterCheckedItem = useMemo(
    () => vendorSearchList?.data.filter((item) => item.isChecked === true),
    [vendorSearchList?.data]
  );

  const addNewBtnStatus = () => {
    return vendorSearchList?.data.some((item) => item?.isNew);
  };

  return (
    <Fragment>
      <Form className="w-100" form={form}>
        <Row justify="space-between" className="py-15 px-14" align="middle">
          <Col className="flex-1">
            <Row align={"middle"} gutter={20}>
              <Col className="d-flex gap-half align-items-center">
                <Checkbox
                  className="normal-checkbox"
                  onChange={(e) => {
                    const updatedData = (vendorSearchList?.data || []).map(
                      (element) => ({
                        ...element,
                        isChecked: e.target.checked,
                      })
                    );
                    dispatch(
                      getVendorSearchUpdate({
                        data: updatedData,
                        actionType: "update",
                      })
                    );
                  }}
                  checked={
                    vendorSearchList?.data.length > 0 &&
                    filterCheckedItem.length === vendorSearchList?.data.length
                  }
                />
                <label className="ml-5 f-14 text-black">
                  {t("translation:default.fields.selectAll")}
                </label>
              </Col>
              <Col sm={12} className="flex-1">
                <Row align="middle" gutter={15}>
                  <Col sm={15} lg={10}>
                    <FormSelectBox
                      className="w-100"
                      margin="m-0"
                      placeholder={`${t(
                        "translation:default.fields.sortBy.label"
                      )}...`}
                      name={"orderByColumn"}
                      options={sortingValuesList}
                    />
                  </Col>
                  <Col>
                    <IconButton
                      onClick={() => setSelectedOrderType(handleSortingToggle)}
                    >
                      <SvgConverter
                        className="mr-5"
                        Icon={
                          selectedOrderType === SORTING_ORDERS.ASC
                            ? FilterAscIcon
                            : FilterDescIcon
                        }
                        height={18}
                        width={18}
                      />
                    </IconButton>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Col>
          <Col>
            <Row gutter={6} align={"middle"}>
              <Col>
                <Button
                  disabled={addNewBtnStatus()}
                  onClick={() => dispatch(addVendorRecord())}
                >
                  {t("translation:purchase-order-view.addNewRecord")}
                </Button>
              </Col>
              <Col>
                <VendorAddressExportDetails
                  filterCheckedItem={filterCheckedItem}
                />
              </Col>
              <Col>
                <TextButton icon={<DeleteIcon />} disabled={true}>
                  <label className="ml-5">
                    {t("translation:default.buttons.delete")}
                  </label>
                </TextButton>
              </Col>
            </Row>
          </Col>
        </Row>
      </Form>
    </Fragment>
  );
};

export default VensorAddressSearchHeader;
