import React, { useMemo } from "react";
import { Col, Row, Typography } from "antd";
import { validateAddressAndConstruct } from "../../../../../../utilz/utilz";
import FiledCard from "../../../../../components/Cards/FiledCard";
import PrimaryLabel from "../../../../../components/Labels/PrimaryLabel";
import Paper from "../../../../../components/Paper/Paper";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../../../utilz/i18n';

const LocationDetailCard = ({ type, data }) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const locationType =
    type === 1
      ? {
          heading: t('translation:purchase-order-view.originDetails'),
          addressTitle: t('translation:default.fields.shipperAddress'),
          details: t('translation:purchase-order-view.shipperDetails'),
        }
      : {
          heading: t('translation:purchase-order-view.destinationDetails'),
          addressTitle: t('translation:default.fields.consigneeAddress'),
          details: t('translation:default.fields.consigneeAddress'),
        };
        

  return (
    <Paper className={`border-muted`}>
      <div className="border-bottom-muted pt-8 pb-10 px-16">
        <Row justify="space-between" align="middle">
          <Col>
            <Row align="middle">
              <Col className="f-16 fw-600">{locationType.heading}</Col>
            </Row>
          </Col>
        </Row>
      </div>
      <div className="p-16">
        <Row className="mb-20">
          <Col sm={4}>
            <div className="field-card-container">
              <Typography.Text className="field-card-title">
                {locationType.details}
              </Typography.Text>
            </div>
          </Col>
          <Col sm={4} className="pr-20">
            <hr />
          </Col>
          <Col sm={4}>
            <div className="field-card-container">
              <Typography.Text className="field-card-title">
                {t('translation:purchase-order-view.wareHouseContactDetails')}
              </Typography.Text>
            </div>
          </Col>
          <Col sm={4} className="pr-20">
            <hr />
          </Col>
          <Col sm={4}>
            <div className="field-card-container">
              <Typography.Text className="field-card-title">
                  {t('translation:purchase-order-view.wareHouseDetails')}
              </Typography.Text>
            </div>
          </Col>
          <Col sm={4} className="pr-20">
            <hr />
          </Col>
        </Row>
        <Row className="mb-10">
          <Col sm={7}>
            <div className="field-card-container">
              <Typography.Text className="field-card-title mb-15">
                {locationType.addressTitle}
              </Typography.Text>
              <FiledCard>
                <PrimaryLabel normal={true}>
                 {data?.name}
                </PrimaryLabel>
              </FiledCard>
              <p className="m-0">
                {/* {validateAddressAndConstruct(data?.originAddress, data?.originCountry, data?.originZip, data?.originCity, data?.originState)} */}
                {data?.addr1}{" "}{data?.addr2}{" "}{data?.city}{" "}{data?.stateDesc}{" "}{data?.countryName}{" "}{data?.zip}
              </p>
            </div>
          </Col>
          <Col sm={1}></Col>
          <Col sm={7}>
            <div className="field-card-container">
              <Typography.Text className="field-card-title">
                {t('translation:purchase-order-view.contactPerson')}
              </Typography.Text>
            </div>
            <p>{data?.contactName}</p>
            <Row>
              <Col sm={8}>
                <div className="field-card-container">
                  <Typography.Text className="field-card-title">
                   {t('translation:purchase-order-view.telephoneNo')}
                  </Typography.Text>
                </div>
                <p>{data?.businessPhone}</p>
              </Col>
              <Col sm={8}>
                <div className="field-card-container">
                  <Typography.Text className="field-card-title">
                    {t('translation:purchase-order-view.extension')}
                  </Typography.Text>
                </div>
                <p></p>
              </Col>
              <Col sm={8}>
                <div className="field-card-container">
                  <Typography.Text className="field-card-title">
                    {t('translation:default.fields.emailAddress')}
                  </Typography.Text>
                </div>
                <p>{data?.email}</p>
              </Col>
            </Row>
          </Col>
          <Col sm={1}></Col>
          <Col sm={5}>
            <div className="field-card-container">
              <Typography.Text className="field-card-title">
                   {t('translation:default.fields.daysOfWeek')}
              </Typography.Text>
              <p>{data?.hrsOfOperation?.split("|")[0]}</p>
            </div>
            <div className="field-card-container">
              <Typography.Text className="field-card-title">
                 {t('translation:default.fields.appointmentType.label')}
              </Typography.Text>
              <p>{type == 1 ? data?.shipperAppointmentType : data?.appointmentType}</p>
            </div>
          </Col>
          <Col sm={3}>
            <div className="field-card-container">
              <Typography.Text className="field-card-title">
                   {t('translation:default.fields.hoursOfOperations')}
              </Typography.Text>
              <p>{data?.hrsOfOperation?.split("|")[1]}</p>
            </div>
          </Col>
        </Row>
      </div>
    </Paper>
  );
};

export default LocationDetailCard;
