import React, { useEffect } from "react";
import ScrollPagination from "../../components/Paginations/ScrollPagination";
import { shallowEqual } from "react-redux";
import { useSelector } from "react-redux";
import VendorSearchCard from "./components/VendorSearch/VendorSearchCard";
import EmptyRecords from "../../components/Common/EmptyRecords";
import {
  getVendorSearchClear,
  getVendorSearchRequest,
  getVendorSearchUpdate,
  updateVendorSearchFilter,
} from "../../../redux/POV/POVActions";
import { useDispatch } from "react-redux";

function VendorAddressSearch() {
  const dispatch = useDispatch();
  

  const selectedCompany = useSelector(
    (state) => state.auth.selectedCompany,
    shallowEqual
  );

  let vendorSearchList = useSelector(
    (state) => state.poVendor.vendorSearchList,
    shallowEqual
  );

  let vendorSearchListFilter = useSelector(
    (state) => state.poVendor.vendorSearchListFilter,
    shallowEqual
  );

  let saveShipper = useSelector(
    (state) => state.poVendor.saveShipper,
    shallowEqual
  );

  let deleteVendorAddress = useSelector(
    (state) => state.poVendor.deleteVendorAddress,
    shallowEqual
  );

  const loadVendorSearchList = () => {
    if (selectedCompany && selectedCompany.companyId) {
      dispatch(getVendorSearchClear());
      let reqParams = {
        ...vendorSearchListFilter,
        vendorKey: selectedCompany?.companyId,
        pageIndex: 1,
      };
      dispatch(updateVendorSearchFilter(reqParams));
      dispatch(getVendorSearchRequest(reqParams));
    }
  };

  useEffect(() => {
    loadVendorSearchList();
  }, [selectedCompany]);

  const handleLoadMore = () => {
    let reqParams = {
      ...vendorSearchListFilter,
      pageIndex: vendorSearchListFilter.pageIndex + 1,
      vendorKey: selectedCompany?.companyId,
    };
    Object.keys(reqParams).forEach((key) =>
      reqParams[key] === undefined ? (reqParams[key] = "") : {}
    );
    dispatch(updateVendorSearchFilter(reqParams));
    dispatch(getVendorSearchRequest(reqParams));
  };

  const hasMoreAction = () => {
    if (vendorSearchList?.progressing === true) {
      return false;
    }
    return vendorSearchList.data.length < vendorSearchList.totalCount;
  };

  const updateSelectedItems = (data) => {
    const updatedData = (vendorSearchList?.data || []).map((element) => {
      if (data.vendorAddressKey === element.vendorAddressKey) {
        return { ...element, isChecked: !element.isChecked };
      }
      return element;
    });

    dispatch(getVendorSearchUpdate({ data: updatedData }));
  };

  const updateEditRecord = (data, value) => {
    if (value === "delete" || value === "refresh") {
      loadVendorSearchList();
      return;
    }
    let updatedData = [];
    if (data?.isNew) {
      updatedData = vendorSearchList?.data.filter((item) => !item?.isNew);
    } else {
      updatedData = vendorSearchList?.data.map((element) => {
        if (data.vendorAddressKey === element.vendorAddressKey) {
          if (typeof value === "boolean") {
            return { ...element, isEditMode: value };
          }
        }
        return element;
      });
    }
    dispatch(
      getVendorSearchUpdate({ data: updatedData, actionType: "update" })
    );
  };

  const renderVendorCard = () => {
    return vendorSearchList?.data && vendorSearchList?.data.length > 0 ? (
      vendorSearchList?.data.map((row, key) => (
        <div className="mb-15" key={key}>
          <VendorSearchCard
            key={row.id}
            index={key}
            saveShipper={saveShipper}
            deleteVendorAddress={deleteVendorAddress}
            data={row}
            updateSelectedItems={updateSelectedItems}
            updateEditRecord={updateEditRecord}
          />
        </div>
      ))
    ) : (
      <>{!vendorSearchList?.progressing && <EmptyRecords />}</>
    );
  };

  return (
    <div className="h-100 pt-15 d-flex flex-direction-column">
      <ScrollPagination
        className={`pb-15 h-100`}
        loadMore={handleLoadMore}
        hasMore={hasMoreAction()}
        isLoading={vendorSearchList?.progressing}
        scrollOffset={100}
      >
        {renderVendorCard()}
      </ScrollPagination>
    </div>
  );
}

export default VendorAddressSearch;
