import { Fragment, useEffect, useMemo, useState } from "react";
import { Col, Form, Row, Spin } from "antd";
import { useDispatch, useSelector } from "react-redux";
import Drawer from "../../../components/Modals/Drawer";
import EmptyRecords from "../../../components/Common/EmptyRecords";
import ScrollPagination from "../../../components/Paginations/ScrollPagination";
import {
  setPurchaseOrderInfo,
  setPurchaseOrderModal,
} from "../../../../redux/purchaseOrder/PurchaseOrderActions";
import { NVISION_USER_TYPES } from "../../../../contants/common";
import {
  cancelShipementInProgress,
  getVendorShipmentDetailListInProgress,
  getVendorShipmentItemDetailListInProgress,
  getVendorShipmentLadingListInProgress,
  getVendorShipmentSummaryListInProgress,
  setPoInfoDefault,
  createShipementInReset,
} from "../../../../redux/POV/POVActions";
import DraftOrderDetailsTab from "../components/Tabs/DraftOrderDetailsTab";
import ContentSpinner from "../../../components/Spinners/ContentSpinner";
import CommonModal from "../../../components/Modals/CommonModal";
import DraftOrderSearchExport from "../components/DraftOrderSearchExport";
import ShipmentDraftSearchDetailCardContainer from "./Component/DetailsCard/ShipmentDraftSearchDetailCardContainer";
import {
  getShipmentDraftInfoListInProgress,
  getShipmentDraftInfoListListInReset,
  setShipmentDraftSearchExportModal,
  updateShipmentDraftInfoListRequest,
} from "../../../../redux/POShipmentDraftSearch/POShipmentDraftSearchActions";
import { APP_ROUTES } from "../../../../contants/RoutesEnum";
import { useLocation, useNavigate } from "react-router-dom";
import { setAppLoader } from "../../../../redux/common/CommonActions";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const ShipmentDraftSearchContent = () => {
  const [activeTabKey, setActiveTabKey] = useState("1");
  const [selectedRecord, setSelectedRecord] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [exportForm] = Form.useForm();
  let navigate = useNavigate();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  const { shipmentInfoList, shipmentInfoListReq, shipmentSearchExportVisible } =
    useSelector((state) => state.poShipmentDraftSearch);

  const { vendorShipmentDetailList, cancelShipment, createShipment } =
    useSelector((state) => state.poVendor);
  let userInfo = JSON.parse(localStorage.getItem("userInfo"));

  const {
    user: { data: userDetails },
    selectedCompany,
  } = useSelector((state) => state?.auth);
  const dispatch = useDispatch();

  useEffect(() => {
    if (createShipment?.success) {
      dispatch(createShipementInReset());
    }
    if (cancelShipment?.success || cancelShipment?.error) {
      dispatch(setAppLoader(false));
    }
    if (cancelShipment?.success) {
      dispatch(getShipmentDraftInfoListListInReset());
      dispatch(
        updateShipmentDraftInfoListRequest({
          ...shipmentInfoListReq,
          pageIndex: 1,
        })
      );
      dispatch(
        getShipmentDraftInfoListInProgress({
          ...shipmentInfoListReq,
          pageIndex: 1,
        })
      );
    }
  }, [cancelShipment]);

  const handleLoadMore = () => {
    dispatch(
      updateShipmentDraftInfoListRequest({
        ...shipmentInfoListReq,
        pageIndex: shipmentInfoListReq.pageIndex + 1,
      })
    );
    dispatch(
      getShipmentDraftInfoListInProgress({
        ...shipmentInfoListReq,
        pageIndex: shipmentInfoListReq.pageIndex + 1,
      })
    );
  };

  const onCloseDrawer = () => {
    setShowModal(false);
    if (userDetails && userDetails?.userType === NVISION_USER_TYPES.CUST) {
      dispatch(setPurchaseOrderInfo({ data: [] }));
    } else {
      dispatch(setPoInfoDefault({ data: [] }));
    }
  };

  const handleTabChange = (key) => {
    setActiveTabKey(key);
  };

  return (
    <Fragment>
      <Drawer
        visible={showModal}
        onClose={onCloseDrawer}
        headerContents={
          <h2 className="header-text m-0">
            {t("translation:purchase-order-view.shipmentNumbers")} :{" "}
            {selectedRecord?.shipmentNo}{" "}
          </h2>
        }
        width={"75%"}
      >
        <div className="d-flex py-16 h-100" style={{ flexDirection: "column" }}>
          {vendorShipmentDetailList?.data?.shipmentDetails &&
          vendorShipmentDetailList?.data?.shipmentDetails.length > 0 ? (
            <>
              <DraftOrderDetailsTab
                handleTabChange={handleTabChange}
                activeKey={activeTabKey}
              />
            </>
          ) : vendorShipmentDetailList?.success === true ? (
            <div className="p-15">
              <EmptyRecords />
            </div>
          ) : (
            ""
          )}

          {vendorShipmentDetailList.progressing ? (
            <div className="p-30 ">
              <ContentSpinner />
            </div>
          ) : (
            ""
          )}
        </div>
      </Drawer>

      <Row gutter={15} className={"h-100 overflow-y-inherit mt-15"}>
        <Col
          sm={24}
          className={
            "h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
          }
        >
          <div className="h-100 overflow-y-inherit d-flex flex-direction-column">
            <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
              {shipmentInfoList.progressing === false &&
                shipmentInfoList?.data.length === 0 && <EmptyRecords />}
              <ScrollPagination
                className={`overflow-x-hidden mb-15 flex-1 ${
                  shipmentInfoList.data?.length > 0 ? "mb-15" : "pb-15"
                } h-100`}
                loadMore={handleLoadMore}
                hasMore={
                  shipmentInfoList?.progressing
                    ? false
                    : shipmentInfoList.data?.length <
                      shipmentInfoList?.totalCount
                }
                isLoading={
                  shipmentInfoList.progressing === true &&
                  shipmentInfoList.data.length > 0
                }
                scrollOffset={100}
              >
                {shipmentInfoList.progressing === true &&
                  shipmentInfoList?.data.length === 0 && (
                    <Spin className="d-flex align-items-center h-100 justify-content-center" />
                  )}
                {shipmentInfoList?.data?.map((item, index) => (
                  <ShipmentDraftSearchDetailCardContainer
                    data={item}
                    key={index}
                    handleDelete={(shipmentKey) => {
                      dispatch(setAppLoader(true));
                      dispatch(cancelShipementInProgress({ shipmentKey }));
                    }}
                    exportSearchShipment={(data) => {
                      dispatch(
                        setShipmentDraftSearchExportModal({
                          show: true,
                          data: [data],
                        })
                      );
                    }}
                    editDraftShipment={(data) => {
                      navigate(
                        `${APP_ROUTES.CREATE_SHIPMENT}/${data.shipmentKey}`
                      );
                    }}
                    onClickContent={(e, data) => {
                      let temp = { ...data };
                      setSelectedRecord(temp);
                      setShowModal(true);
                      temp["vendorKey"] = selectedCompany.companyId;
                      temp["customerKey"] = shipmentInfoListReq.customerKey;
                      temp["shipmentKey"] = temp.shipmentKey;
                      temp["shipmentType"] = "Draft";
                      temp["userGuid"] = userInfo?.userGuid;
                      temp["vendorLogonID"] = userInfo?.email;

                      let tempObj = {};
                      tempObj["shipmentKey"] = temp.shipmentKey;
                      tempObj["shipmentType"] = "Draft";
                      tempObj["userGuid"] = userInfo?.userGuid;
                      tempObj["customerKey"] = shipmentInfoListReq.customerKey;
                      dispatch(getVendorShipmentDetailListInProgress(tempObj));
                      dispatch(getVendorShipmentSummaryListInProgress(tempObj));
                      dispatch(getVendorShipmentLadingListInProgress(tempObj));
                    }}
                  />
                ))}
              </ScrollPagination>
            </div>
          </div>
        </Col>
      </Row>

      <CommonModal
        className="vw-80"
        open={shipmentSearchExportVisible}
        title={t("translation:purchase-order-view.draftOrderSearchExport")}
        okText={t("translation:default.buttons.export")}
        cancelText={t("translation:default.buttons.close")}
        onClose={() => {
          dispatch(
            setShipmentDraftSearchExportModal({ show: false, data: [] })
          );
        }}
        onCancel={() => {
          dispatch(
            setShipmentDraftSearchExportModal({ show: false, data: [] })
          );
        }}
        onOk={() => {
          exportForm.submit();
        }}
      >
        <DraftOrderSearchExport form={exportForm} typeOfForm="draft" />
      </CommonModal>
    </Fragment>
  );
};

export default ShipmentDraftSearchContent;
