import React, { Fragment, useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Col, Form, Row, notification } from "antd";
import _, { cloneDeep, find, keyBy, omitBy } from "lodash";
import FilterCard from "../../../components/Cards/FilterCard";
import FormCheckBox from "../../../components/Inputs/FormCheckBox";
import { DRAFT_ORDER_SEARCH_EXPORT_FILEDS } from "../../../../contants/POVendorSearchAddressEnums";
import { exportShipmentListToExcel } from "../../../../utilz/exportToExcel";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const DraftOrderSearchExport = ({ form, exportType = "", typeOfForm = "" }) => {
  const [vendorInfoAll, setVendorInfoAll] = useState(true);
  const vendorFields = [...DRAFT_ORDER_SEARCH_EXPORT_FILEDS];
  const [selectedObjVal, setSelectedObjVal] = useState({});
  const [api, contextHolder] = notification.useNotification();
  const dispatch = useDispatch();
  const { shipmentSearchExportData } = useSelector(
    (state) => state.poShipmentSearch
  );

  const { shipmentDraftSearchExportData } = useSelector(
    (state) => state.poShipmentDraftSearch
  );

  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  let exportData = {};
  if (typeOfForm === "shipment") {
    exportData = shipmentSearchExportData;
  } else {
    exportData = shipmentDraftSearchExportData;
  }

  useEffect(() => {
    if (form) {
      handleSelectAll(vendorFields, true);
    }
  }, [form]);

  const handleSelectAll = (data, value) => {
    data.forEach(({ name }) => {
      form.setFieldValue(name, value);
    });
  };

  // useEffect(() => {
  //   if (excelData.progressing === false && excelData.success === true) {
  //     generateExportValuesWithFields(selectedObjVal, excelData.data);
  //   } else if (excelData.progressing === false && excelData.error === true) {
  //     api["error"]({
  //       message: "Visibility Export Excel",
  //       description: excelData.data?.message,
  //     });
  //   }
  // }, [excelData]);

  const handleSubmit = (data) => {
    // let req = {
    //   ...visibilitySearchList.req,
    //   userType: userDetails?.userType,
    //   customerKey:
    //     userDetails?.userType === "CUST" ? `${selectedCompany.companyId}` : "",
    //   carrierKey:
    //     userDetails?.userType === "CARR" ? `${selectedCompany.companyId}` : "",
    //   vendorKey: visibilitySearchList?.req?.supplier || "",
    //   pageIndex: 1,
    //   pageSize: visibilitySearchList?.count?.totalCount || 10,
    // };
    // delete req.pageIndex;
    // dispatch(getVisibilityExcelData(req));

    let selectedFields = omitBy(data, (val) => !val);
    let selectedObj = vendorFields.filter(
      (item) => selectedFields[item.name] === true
    );
    setSelectedObjVal(cloneDeep(selectedObj));

    generateExportValuesWithFields(cloneDeep(selectedObj), exportData?.data);
  };

  const generateExportValuesWithFields = (columns, data) => {
    let rows = [];
    data.forEach((row) => {
      let arr = [];
      columns.forEach(({ name }) => {
        if (Object.hasOwnProperty.call(row, name)) {
          arr.push(row[name]);
        } else {
          arr.push("");
        }
      });

      rows.push(arr);
    });
    columns = columns.map(({ label }) => ({
      name: label.length > 0 ? label : "---",
    }));
    exportShipmentListToExcel(columns, rows);
  };

  return (
    <Fragment>
      {contextHolder}
      {/* {excelData.progressing === true && (
        <div className="d-flex justify-content-center align-items-center h-100">
          <Spin spinning={excelData.progressing} tip="Loading..."></Spin>
        </div>
      )} */}
      <Form
        onFinish={handleSubmit}
        className="h-100 custom-scroll-bar"
        form={form}
      >
        <FilterCard
          showBorder={true}
          title={
            <FormCheckBox
              checkBoxClass={"normal-checkbox"}
              label={
                typeOfForm === "shipment"
                  ? t('translation:purchase-order-view.shipmentSearchInformation')
                  : t('translation:purchase-order-view.draftOrderSearchInformation')
              }
              className={"m-0"}
              checked={vendorInfoAll}
              onChange={(e) => {
                setVendorInfoAll(e.target.checked);
                handleSelectAll(vendorFields, e.target.checked);
              }}
            />
          }
          className={"mb-20"}
        >
          <Row className={"px-16 pt-15"}>
            {vendorFields.map((item, index) => (
              <Fragment key={index}>
                <Col lg={6}>
                  <Col lg={24}>
                    <FormCheckBox
                      checkBoxClass={"normal-checkbox"}
                      label={item.label}
                      name={item.name}
                      className={"mb-15"}
                    />
                  </Col>
                </Col>
              </Fragment>
            ))}
          </Row>
        </FilterCard>
      </Form>
    </Fragment>
  );
};

export default DraftOrderSearchExport;

