import { Col, Collapse, Form, Row } from "antd";
import CustomerNameSelector from "../../../../../components/Selectors/CustomerNameSelector";
import FormSelectBox from "../../../../../components/Inputs/FormSelectBox";
import FormLabel from "../../../../../components/Labels/FormLabel";
import FormDatePicker from "../../../../../components/Inputs/FormDatePicker";
import { DRAFT_SEARCH_TYPES_OPTION } from "../../../../../../contants/common";
import FormInput from "../../../../../components/Inputs/FormInput";
import { useLocation } from "react-router-dom";
import { useMemo } from "react";
import { routeFileSelector } from "../../../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const ShipmentDraftSearchFilterContent = ({ form }) => {
  const createdOnFrom = Form.useWatch("createdOnFrom", form);
  const createdOnTo = Form.useWatch("createdOnTo", form);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <div className="px-16">
      <Row>
        <Col sm={24}>
          <CustomerNameSelector 
          name={"customerKey"} 
          form={form} 
          setDetafuleValue={true}
          rules={[{ required: true, message: t('translation:default.fields.customerName.placeholderEnter') },]}/>
        </Col>
      </Row>
      <Row>
        <Col sm={24}>
          <FormInput
            editMode={true}
            label={t('translation:default.fields.shipmentNumber.label')}
            placeholder={t('translation:default.fields.shipmentNumber.label')}
            name="shipmentNo"
            margin={"mb-15"}
          />
        </Col>
      </Row>
      <Row>
        <Col sm={24}>
          <FormLabel>{t('translation:purchase-order-view.shipmentCreateDate')}</FormLabel>
          <Row className={"mt-3 align-items-center"} gutter={5}>
            <Col sm={6}>
              <FormLabel>{t('translation:default.fields.from.label')}</FormLabel>
            </Col>
            <Col sm={18}>
              <FormDatePicker
                placeholder={t('translation:default.fields.from.placeholder')}
                margin={"mb-0"}
                name={"createdOnFrom"}
                disableFuture={true}
                disabledTargetDate={createdOnTo}
              />
            </Col>
          </Row>
          <Row className={"mt-6 align-items-center mt-16"} gutter={5}>
            <Col sm={6}>
              <FormLabel>{t('translation:default.fields.to.label')}</FormLabel>
            </Col>
            <Col sm={18}>
              <FormDatePicker
                name="createdOnTo"
                placeholder={t('translation:default.fields.to.placeholder')}
                margin={"mb-0"}
                disableFuture={false}
                disabledTargetDate={createdOnFrom}
              />
            </Col>
          </Row>
        </Col>
      </Row>
      <Row className="mt-15">
        <Col sm={24}>
          <FormSelectBox
            label={t('translation:default.fields.shipmentType.label')}
            className="w-100"
            margin="m-0"
            placeholder={t('translation:default.fields.shipmentType.placeholder')}
            name={"shipmentType"}
            disabled={true}
            options={DRAFT_SEARCH_TYPES_OPTION}
          />
        </Col>
      </Row>
      <Row className="mt-15">
        <Col sm={24}>
          <FormInput
            editMode={true}
            label={t('translation:default.fields.purchaseOrderNumber')}
            placeholder={t('translation:default.fields.purchaseOrderNumber')}
            name="poNumber"
            margin={"mb-15"}
          />
        </Col>
      </Row>
    </div>
  );
};

export default ShipmentDraftSearchFilterContent;
