import React, { useMemo } from "react";
import WithFilter from "../../screens/common/WithFilter";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';
import ShipmentDraftSearchFilter from "../../screens/PurchaseOrderScreens/ShipmentDraftSearch/ShipmentDraftSearchFilter";
import ShipmentDraftSearchHeader from "../../screens/PurchaseOrderScreens/ShipmentDraftSearch/ShipmentDraftSearchHeader";
import ShipmentDraftSearchContent from "../../screens/PurchaseOrderScreens/ShipmentDraftSearch/ShipmentDraftSearchContent";
import ShipmentDraftSearchFooter from "../../screens/PurchaseOrderScreens/ShipmentDraftSearch/ShipmentDraftSearchFooter";

const DraftSearchPage = () => {
  const { pathname: activeTab} = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <WithFilter
      filterTitle={t('translation:purchase-order-view.draftOrderSearch')}
      pageTitle={t('translation:purchase-order-view.shipmentInformation')}
      headerContent={<ShipmentDraftSearchHeader />}
      filterElement={<ShipmentDraftSearchFilter />}
      contentElement={<ShipmentDraftSearchContent />}
      footerContent={<ShipmentDraftSearchFooter/>}
    />
  );
};

export default DraftSearchPage;
