import React, { useMemo } from "react";
import { Col, Form, Row } from "antd";
import { SORTING_ORDERS } from "../../../../../../contants/common";
import IconButton from "../../../../../components/Buttons/IconButton";
import SvgConverter from "../../../../../components/Common/SvgConverter";
import FormSelectBox from "../../../../../components/Inputs/FormSelectBox";
import { ReactComponent as FilterAscIcon } from "../../../../../../assets/icons/filter-asc.svg";
import { ReactComponent as FilterDescIcon } from "../../../../../../assets/icons/filter-desc.svg";
import { SORTING_FIELDS_SHIPMENT_ORDER } from "../../../../../../contants/common";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import HiddenFormInput from "../../../../../components/Inputs/HiddenFormInput";
import { getShipmentInfoListInProgress, getShipmentInfoListListInReset, updateShipmentInfoListRequest } from "../../../../../../redux/POShipmentSearch/POShipmentSearchActions";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../../../utilz/i18n';

const ShipmentSearchListFilter = () => {
  const { shipmentInfoListReq } = useSelector(
    (state) => state.poShipmentSearch
  );

  const [form] = Form.useForm();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  const dispatch = useDispatch();

  const handleSortingToggle = (oldType) => {
    if (oldType === SORTING_ORDERS.ASC) {
      return SORTING_ORDERS.DESC;
    } else if (oldType === SORTING_ORDERS.DESC) {
      return SORTING_ORDERS.ASC;
    }
  };

  const fetchShipment = (OrderBySort, OrderByColumn) => {
    dispatch(getShipmentInfoListListInReset())
      dispatch(
        updateShipmentInfoListRequest({
          pageIndex: 1,
          orderByColumn:OrderByColumn,
          orderBySort:OrderBySort
       })
      );
      dispatch(getShipmentInfoListInProgress({
        ...shipmentInfoListReq,
        pageIndex: 1,
        orderByColumn:OrderByColumn,
        orderBySort:OrderBySort
      }));
  };

  const OrderBySort = Form.useWatch("OrderBySort", form);
  const OrderByColumn = Form.useWatch("OrderByColumn", form);

  useEffect(() => {
    if (OrderBySort && OrderByColumn) {
      fetchShipment(OrderBySort, OrderByColumn);
    } else {
      dispatch(
        updateShipmentInfoListRequest({
         orderByColumn:"",
          orderBySort:""
       })
      );
    }
  }, [OrderBySort, OrderByColumn]);

  return (
    <Form
      className="w-100"
      form={form}
      initialValues={{ OrderBySort: SORTING_ORDERS.ASC }}
    >
      <Row align="middle" gutter={15}>
        <Col sm={16} lg={16}>
          <FormSelectBox
            options={SORTING_FIELDS_SHIPMENT_ORDER}
            className="w-100"
            margin="m-0"
            placeholder={`${t('translation:default.fields.sortBy.label')}...`}
            name={"OrderByColumn"}
            label={""}
          />
        </Col>
        <Col>
          {OrderBySort && (
            <IconButton
              onClick={() =>
                form.setFieldValue(
                  "OrderBySort",
                  handleSortingToggle(OrderBySort)
                )
              }
            >
              <SvgConverter
                className="mr-5"
                Icon={
                  OrderBySort === SORTING_ORDERS.ASC
                    ? FilterAscIcon
                    : FilterDescIcon
                }
                height={18}
                width={18}
              />
            </IconButton>
          )}
        </Col>
      </Row>
      <HiddenFormInput name="OrderBySort" />
    </Form>
   
  );
};

export default ShipmentSearchListFilter;
