import { Col, Form, Row } from "antd";
import { useDispatch, useSelector } from "react-redux";
import Button from "../../../components/Buttons/Button";
import { SHIPMENT_SEARCH_DATE_FILEDS } from "../../../../contants/Fields";
import { PURCHASE_ORDER_DATE_FORMAT } from "../../../../contants/common";
import { useEffect, useMemo, useState } from "react";
import ShipmentDraftSearchFilterContent from "./Component/Search/ShipmentDraftSearchFilterContent";
import {
  getShipmentDraftInfoListInProgress,
  getShipmentDraftInfoListListInReset,
  updateShipmentDraftInfoListRequest,
} from "../../../../redux/POShipmentDraftSearch/POShipmentDraftSearchActions";
import { setSelectedShipmentSummary } from "../../../../redux/POV/POVActions";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const ShipmentDraftSearchFilter = () => {
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const { shipmentInfoListReq } = useSelector(
    (state) => state.poShipmentDraftSearch
  );

  const {
    user: { data: userDetails },
    selectedCompany,
  } = useSelector((state) => state?.auth);

  const getShipmentSearchList = (data) => {
    // console.log("setSelectedShipmentSummary");
    dispatch(setSelectedShipmentSummary([]));
    dispatch(getShipmentDraftInfoListListInReset());
    for (const key in data) {
      if (SHIPMENT_SEARCH_DATE_FILEDS.includes(key) && data[key]) {
        data[key] = data[key].format(PURCHASE_ORDER_DATE_FORMAT);
      }
    }
    Object.keys(data).forEach((key) => {
      if (data[key] === undefined) {
        data[key] = "";
      }
    });

    let req = {
      ...shipmentInfoListReq,
      ...data,
      pageIndex:1
    };
    dispatch(
      updateShipmentDraftInfoListRequest({
        ...data,
        pageIndex:1
      })
    );
    dispatch(getShipmentDraftInfoListInProgress(req));
  };

  useEffect(() => {
    setTimeout(() => {
      form.resetFields();
    }, 1000);
    dispatch(getShipmentDraftInfoListListInReset());
    dispatch(
      updateShipmentDraftInfoListRequest({
        pageIndex: 1,
        vendorKey: selectedCompany?.companyId,
      })
    );
  }, [selectedCompany?.companyId]);

  const customerKey = Form.useWatch("customerKey", form);

  useEffect(() => {
    if (selectedCompany?.companyId && customerKey) {
      dispatch(getShipmentDraftInfoListListInReset());
      dispatch(
        updateShipmentDraftInfoListRequest({
          customerKey: customerKey,
          pageIndex: 1,
          vendorKey: selectedCompany?.companyId,
        })
      );
      dispatch(
        getShipmentDraftInfoListInProgress({
          ...shipmentInfoListReq,
          customerKey: customerKey,
        })
      );
    }
   
  }, [customerKey]);

  return (
    <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
      <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1">
        <Form
          onFinish={getShipmentSearchList}
          form={form}
          initialValues={{
            isActive: true,
            isApproved: true,
            shipmentType: "Draft",
            customerKey: customerKey,
          }}
        >
          <ShipmentDraftSearchFilterContent form={form} />
        </Form>
      </div>
      <Row className="px-15" gutter={15}>
        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"default"}
            onClick={() => {
              let customerKey = form.getFieldValue("customerKey");
              form.resetFields();
              form.setFieldsValue({
                customerKey: customerKey,
                isActive: true,
                isApproved: true,
                shipmentType: "Draft",
              });
            }}
            buttonType="button"
          >
            {t('translation:default.buttons.clear')}
          </Button>
        </Col>
        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"primary"}
            onClick={() => form.submit()}
            buttonType="submit"
          >
            {t('translation:default.buttons.search')}
          </Button>
        </Col>
      </Row>
    </div>
  );
};

export default ShipmentDraftSearchFilter;
