import React, { useMemo } from "react";
import { Form, Row, Table, Col } from "antd";
import { useSelector } from "react-redux";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../utilz/i18n";

const ShipmentItemDetails = () => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  const { vendorShipmentDetailList } = useSelector(
    (state) => state.poVendor
  );

  const data = vendorShipmentDetailList?.success
    ? vendorShipmentDetailList?.data?.shipmentDetails
    : [];

  const columns = [
    {
      title: t('translation:default.fields.productCode.label'),
      dataIndex: "productCode",
      render: (text) => <span>{text}</span>,
      width: "10%",
    },
    {
      title: t("translation:default.fields.productDesc.label"),
      dataIndex: "productDescription",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.quantity.label"),
      dataIndex: "quantity",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:purchase-order-view.chargableWeight"),
      dataIndex: "weight",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t(
        "translation:purchase-order-view.fields.weightUnitOfMeasure.label"
      ),
      dataIndex: "weightUOMDesc",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.cube.label"),
      dataIndex: "cube",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:purchase-order-view.fields.class.label"),
      dataIndex: "class",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.unitOfMeasure"),
      dataIndex: "billQtyUOMDesc",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:purchase-order-view.htsCode"),
      dataIndex: "hts",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.eccnNumber"),
      dataIndex: "eccn",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.countryOfOrigin"),
      dataIndex: "countryOfOrigin",
      render: (text) => <span>{text}</span>,
    },
    {
      title: t("translation:default.fields.packageID.label"),
      dataIndex: "crateNo",
      render: (text) => <span>{text}</span>,
    },
  ];

  return (
    <Form>
      <div className="p-20">
        <Row className="align-items-center mb-15" justify="space-between">
          <Col>
            <h2 className="header-text m-0">
              <span className="primary-text f-18">
                {t("translation:purchase-order-view.shipmentItemDetails")}
              </span>
            </h2>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col sm={24}>
            <Table
              className="auction-product-detail"
              columns={columns}
              dataSource={data}
              bordered
              pagination={false}
              scroll={{ y: "calc(100vh - 50vh)" }}
            />
          </Col>
        </Row>
      </div>
    </Form>
  );
};

export default ShipmentItemDetails;
